html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  background-color: rgb(167, 167, 167);
}

.shop-container {
  background-color: rgb(167, 167, 167);
}

.body {
  top: -200px;
}

.activeButton {
  background-color: teal;
  border: 3px solid rgba(255, 255, 255, 0.5);
}

.divider {
  margin: 20px;
}

.links {
  display: flex;
  position: absolute;
  top: 233px;
  left: 0px;
  max-width: 100%;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size-adjust: initial;
  margin-top: 30px;
  margin-bottom: 265px;
}

.text-white.navbar {
  width: 100%;
  position: fixed;
  z-index: 2;
  background-image: linear-gradient(35deg, teal, rgb(62, 180, 135));
  box-shadow: 0px 5px 23px rgba(0, 0, 0, 0.301),
    0px 10px 25px rgba(0, 0, 0, 0.507);
}

.row.navbar-div {
  margin-bottom: 30px;
}

.categories-header {
  font-weight: 700;
  color: white;
  text-shadow: 3px 1.7px 1px rgba(0, 0, 0, 0.15);
}

.main-body-div {
  display: flex;
  max-width: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pagination-controls {
  display: flex;
  flex-direction: column;
  width: auto;
  align-items: center;
  padding: 30px;
  justify-content: center;
  background-color: transparent;
  margin-top: 300px !important;
}

.form-control {
  margin-top: 20px;
}

.navbar-cart {
  position: relative;
}

.table-row {
  background-color: teal;
}

.cart-view {
  background: teal;
  max-width: 100vw;
  display: flex;
  justify-content: space-between;
  text-align: center;
  align-items: center;
  flex-direction: column;
  box-shadow: 1px 6px 6px rgba(0, 0, 0, 0.1),
    -6px -6px 6px rgba(255, 255, 255, 0.1);
}

.btn.btn-secondary.m-1.checkout {
  box-shadow: inset -1px 0 0 rgb(47, 255, 141), inset 0 -1px 0 rgb(47, 255, 210),
    inset 1px 0 0 rgb(47, 255, 116), inset 0 1px 0 rgb(47, 255, 109);
}

.btn.btn-secondary.m-1.checkout:hover {
  background-color: greenyellow;
  color: #333;
  transition: colorChange ease-in-out;
  animation: colorChange 0.6s forwards;
}

.pagination-div {
  max-width: 90vw;
  width: 50vw;
  padding: 10px;
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
}

.logo-icon {
  width: 50px;
  margin-right: 10px;
}

.footer-div {
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn.btn-block.btn-secondary.m-2.col-3.admin-btn {
  display: flex;
  justify-content: center;
  margin-top: auto;
  padding: 10px 50px 10px 50px;
}

.btn.btn-primary.m-1:focus {
  color: teal;
}

@keyframes colorChange {
  from {
    background-color: #777;
  }
  to {
    background-color: greenyellow;
  }
}

@keyframes colorChange2 {
  from {
    background-color: #777;
  }
  to {
    background-color: rgb(255, 255, 255);
  }
}

.btn.btn-primary.m-1 {
  box-shadow: inset -1px 0 0 rgb(47, 255, 141), inset 0 -1px 0 rgb(47, 255, 210),
    inset 1px 0 0 rgb(47, 255, 116), inset 0 1px 0 rgb(47, 255, 109);
}

.add-to-cart-button-div {
  display: flex;
  width: 100%;
  margin: 10px;
}

.view-image-button-div {
  justify-content: center;
  display: flex;
}

.no-products-text {
  color: #fff;
  margin-top: 20px;
  font-size: 40px;
  text-align: center;
}

.cart-item-amount-text {
  background-color: rgb(9, 93, 93);
  padding: 4px;
  padding-bottom: 7px;
  margin-right: 3px;
}

.view-image-button {
}

.table-headers {
  -webkit-text-fill-color: rgb(
    255,
    255,
    255
  ); /* Will override color (regardless of order) */
  -webkit-text-stroke-width: 0.4px;
  -webkit-text-stroke-color: rgba(255, 255, 255, 0.2);
  font-size: 10px;
  text-shadow: 2px 2.3px 3px rgba(0, 0, 0, 0.3);
  background-color: teal;
}

.subtotal-amount {
  font-size: 14px;
  color: greenyellow;
}

.price-amount {
  font-size: 14px;
  color: white;
}

.cart-total-text {
  color: greenyellow;
  text-shadow: 2px 4px 2px rgba(0, 0, 0, 0.3);
  font-size: 16px;
  background-color: #333;
}

.product-name {
  color: #fff;
  text-shadow: 2px 4px 4px rgba(0, 0, 0, 0.1);
  font-weight: bold;
  -webkit-text-stroke-width: 0.2px;
  -webkit-text-stroke-color: teal;
  font-size: 16px;
}

.btn.btn-sm.btn-danger.mx-1 {
  background: linear-gradient(
      217deg,
      rgba(255, 0, 0, 0.8),
      rgba(255, 0, 0, 0) 70.71%
    ),
    linear-gradient(127deg, rgba(98, 0, 0, 0.8), rgba(0, 255, 0, 0) 70.71%),
    linear-gradient(336deg, rgba(0, 0, 255, 0.8), rgba(0, 0, 255, 0) 70.71%) !important;
  font-size: 10px;
  font-weight: bold;
}

.btn.btn-sm.btn-danger.mx-1:hover {
  color: #dc3545 !important;
  text-shadow: 1px 0.5px 0.1px rgb(255, 255, 255, 0.9);
  background: linear-gradient(
      217deg,
      rgba(238, 235, 235, 0.8),
      rgb(243, 203, 203) 70.71%
    ),
    linear-gradient(
      127deg,
      rgba(255, 255, 255, 0.8),
      rgba(255, 255, 255, 0) 70.71%
    ),
    linear-gradient(336deg, rgb(229, 226, 226), rgba(255, 255, 255, 0) 70.71%) !important;
}

.btn.btn-primary {
  border: 2px solid teal;
  background: linear-gradient(243deg, teal, rgba(18, 117, 134, 0.363)),
    linear-gradient(216deg, rgba(63, 243, 204, 0.9), rgba(143, 143, 143, 0.3));
  font-weight: bold;
  text-shadow: 1.5px 1px 0.5px rgba(0, 0, 0, 0.5);
}

.btn.btn-primary:hover {
  background-color: teal;
  border: 2px solid teal;
  text-shadow: none;
}

.btn.btn-secondary,
.btn.btn-secondary.mx-1 {
  background: linear-gradient(35deg, rgb(223, 223, 223), rgb(243, 237, 237)),
    linear-gradient(216deg, rgb(223, 235, 235), rgb(255, 255, 255));
  color: teal;
  text-shadow: none;
  text-shadow: 1px 1px 1px rgba(255, 255, 255, 0.7);
  border: 1px solid #999;
}

.btn.btn-secondary:hover,
.btn.btn-secondary.mx-1:hover {
  background: linear-gradient(
    238deg,
    rgba(238, 237, 237, 0.9),
    rgba(173, 218, 231, 0.151)
  );
  color: rgb(80, 80, 80);
  border: 1px solid #888;
}

.create-product-button {
  padding: 8px !important;
}

.create-product-button i {
  padding-left: 3px;
  font-weight: bold;
}

.action-buttons {
  margin-top: 30px;
  width: 50%;
  display: flex;
  flex-direction: column;
}

.quantity-input {
  width: fit-content;
  line-height: 2.65;
  margin: 0;
  font-size: small;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  opacity: 1;
}

input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: scrollbarthumb-vertical;
}

input[type="number"] {
  -webkit-appearance: textfield;
  -moz-appearance: textfield;
  appearance: textfield;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
}

.number-input {
  border: 2px solid #ddd;
  display: inline-flex;
}

.number-input,
.number-input * {
  box-sizing: border-box;
}

.number-input button {
  outline: none;
  -webkit-appearance: none;
  background-color: white;
  border: none;
  align-items: center;
  justify-content: center;
  width: 6px;
  padding: 5px;
  height: 1.3rem;
  cursor: pointer;
  margin: 0;
  position: relative;
}

.number-input button:before,
.number-input button:after {
  display: inline-block;
  position: absolute;
  content: "";
  width: 0.5rem;
  height: 2px;
  background-color: rgb(29, 29, 29);
  box-shadow: inset 1px 0px 1.2px rgba(0, 0, 0, 0.8);
  transform: translate(-50%, -50%);
}
.number-input button.plus:after {
  transform: translate(-50%, -50%) rotate(90deg);
}

.number-input input[type="number"] {
  font-family: sans-serif;
  max-width: 2.2rem;
  padding: 0.5rem;
  border: solid #ddd;
  border-width: 0 2px;
  font-size: 0.9rem;
  height: 1.3rem;
  font-weight: bold;
  text-align: center;
}

.badge-primary.price-tag {
  border: 3px solid teal;
  background-color: white;
  color: teal;
  position: absolute;
  right: 20px;
  font-size: x-large;
}

.to-top-text {
  padding: 10px;
  font-size: 14px;
}

.to-top.scroll-arrow {
  transform: scale(0.5);
  border-radius: 10px;
  color: white;
  width: 100px;
  padding-top: 2.6em;
  display: block; /* or block */
  position: fixed;
  bottom: 0px;
  right: -5px;
  text-align: center;
  border-color: white;
  text-decoration: none;
  transition: all 0.3s ease-out;
  background-color: rgb(53, 53, 53);
  box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.3);
}
.to-top.scroll-arrow:before {
  content: "â–²";
  font-size: 1.4em;
  position: absolute;
  top: 10px;
  left: 50%;
  margin-left: -0.7em;
  margin-bottom: 5px;
  border: solid 0.2em rgb(49, 49, 49);
  border-radius: 10em;
  width: 1.7em;
  height: 1.7em;
  line-height: 1.2em;
  border-color: inherit;
  transition: transform 0.6s ease-in;
}

.to-top.scroll-arrow:hover {
  color: teal;
  border-color: white;
  background-color: rgb(12, 12, 12);
  box-shadow: 6px 6px 6px rgba(0, 0, 0, 0.3), -6px -6px 15px rgba(0, 0, 0, 0.35);
  transform: scale(0.6);
}

input[type="search"] {
  padding: 5px; /* Overridden by padding: 1px; */
  font-family: Arial, Helvetica, sans-serif; /* Overridden by font: -webkit-small-control; */
  border: 1px solid teal; /* Overridden by border: 2px inset; */
  background: rgb(255, 255, 255); /* Overridden by background-color: white; */
  line-height: 1; /* Irrelevant, I guess */
  color: teal;
  outline: none;
  width: 150px;
}

.search-btn {
  font-size: small;
  height: 28px;
  position: relative;
  bottom: 1.5px;
  width: 60px;
  color: teal;
  font-weight: 600;
  font-family: monospace;
  border: none;
  outline: none;
}

.search-btn:focus {
  outline-color: rgb(50, 92, 92);
}

.navbar-search-span {
  background-color: rgb(6, 80, 80);
  height: 100%;
  padding: 6px;
}

.navbar-search {
  padding: 10px;
  width: 400px;
  position: absolute;
  right: 100px;
}

.navbar-search-input {
  border: none;
  border-style: hidden;
}

.to-top.to-top.scroll-arrow:hover h3 {
  font-size: 20px;
}

.to-top.scroll-arrow:hover:before {
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.35);
}

.product-title {
  color: #333;
  text-align: left;
  font-weight: bold;
  background-color: #eee;
  padding: 10px;
  margin: 10px;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.category-name {
  background-color: rgb(10, 73, 73);
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  padding-right: 20px;
