}

.product-details {
  margin: 5px;
  border: 3px solid teal;
  background-color: teal;
  height: fit-content;
  width: 100%;
}

.btn.btn-sm.btn-warning {
  font-size: 10px;
  font-weight: bold;
}

.btn-warning:hover {
