}

.product-image {
  width: auto;
  margin: auto;
  height: 200px;
}

.product-image-div {
  display: flex;
  align-items: center;
  background-color: #333;
  justify-content: center;
}

.product-description-text {
  background-color: #aaa;
  box-shadow: 0px 6px 2px #0000001a;
  padding: 20px;
}

.btn.btn-success.add-to-cart-button {
}

.btn.btn-success.add-to-cart-button {
  border-radius: 20px;
  box-shadow: inset -1px 0 0 greenyellow, inset 0 -1px 0 greenyellow,
    inset 1px 0 0 greenyellow, inset 0 1px 0 greenyellow;
}

.btn.btn-success.btn-sm.float-right.add-to-cart-button:hover {
  transition: ease-in-out;
  animation: colorChange3 0.6s forwards;
}

@keyframes colorChange3 {
  from {
    background-color: #fff;
    box-shadow: inset -1px 0 0 #333, inset 0 -1px 0 #333, inset 1px 0 0 #333,
      inset 0 1px 0 #333;
  }
  to {
    background-color: greenyellow;
    color: #333;
    box-shadow: inset -3px 0 0 teal, inset 0 -3px 0 teal, inset 3px 0 0 teal,
      inset 0 3px 0 teal;
  }
}

